* {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	vertical-align: baseline;
	outline: none;
}
$black: #000000;
$white: #FFFFFF;
$commit-green: #9BE9A8;
$success: #40C463;
$warning: #EFB008;
$error: #D83232;
.swiper {
	width: 100%;
	height: 100%;
	position: relative;
}

.swiper-slide {
	text-align: center;
	font-size: 18px;
	height: 100vh;
	background: $black;
	color: $white;
}


.swiper-pagination {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 10px;
	display: flex;
	flex-direction: column;
	gap: 8px;
}

.swiper-pagination-bullet {
	width: 10px;
	height: 10px;
	border: 1px solid white;
	border-radius: 50%;
}

.swiper-pagination-bullet-active {
	background-color: white;
}
