.title {
	font-size: 30px;
	margin-bottom: 8px;
}

.text {
	font-family: 'Approach';
	font-weight: 400;
	font-size: 18px;
}


* {
  box-sizing: border-box;
  scrollbar-width: none;
  scroll-behavior: smooth;
}

*::-webkit-scrollbar {
  display: none;
  -webkit-appearance: none;
}

html, body {
	height: 100%;
	background-color: $black;
}



.hero {

	&__logo {
		width: 100px;
		margin-bottom: 24px;
	}

	&__title {
	}

}
.section {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;	
	padding: 32px 48px 32px 32px;;
}

.highlight {
	color: $commit-green;
}

.pagination {
	display: flex;
	align-items: center;

	&:after, &::before {
		content: "x";
		height: 50px;
		width: 1px;
		background-color: $white;
	}

}
.stay-tuned {
	border: 10px solid red;

	&__text {
		margin-bottom: 32px;
	}

	&__input {
		background-color: opacify($white, 0.3);

	}

}